.win-lose-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background: #ffffff33;
  border-radius: 10px;
  padding: 20px;
}

.result-image {
  width: 200px;
  margin: 10px;
}

.result-content {
  text-align: center;
  color: #3d3d3d;
}

.result-text {
  font-size: 24px;
  font-weight: 600;
}

.score-label {
  font-size: 18px;
}

.score-value {
  font-size: 20px;
  font-weight: bold;
}

.play-again-button {
  background-color: #ffce27;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 10px;
}
