/* Basic layout */
.emoji-game-container {
  min-height: 100vh;
  background: linear-gradient(to bottom, #9796f0, #fbc7d4);
  font-family: Roboto, sans-serif;
}

.emojis-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin: 20px auto;
  max-width: 900px;
}
